.ncf-container {
  font-size: 14px;
  box-sizing: border-box;
  position: fixed;
  z-index: 999999;
}
.ncf-container.nfc-top-left {
  top: 12px;
  left: 12px;
}
.ncf-container.nfc-top-right {
  top: 12px;
  right: 12px;
}
.ncf-container.nfc-bottom-right {
  bottom: 12px;
  right: 12px;
}
.ncf-container.nfc-bottom-left {
  bottom: 12px;
  left: 12px;
}
@media (max-width: 767px) {
  .ncf-container {
    left: 0;
    right: 0;
  }
}
.ncf-container .ncf {
  background: #fff;
  transition: 0.3s ease;
  position: relative;
  pointer-events: auto;
  overflow: hidden;
  margin: 0 0 6px;
  padding: 30px;
  width: 300px;
  border-radius: 3px 3px 3px 3px;
  box-shadow: 0 0 12px #999;
  color: #000;
  opacity: 0.9;
  -ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=90);
  filter: alpha(opacity=90);
  background-position: 15px !important;
  background-repeat: no-repeat !important;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.ncf-container .ncf:hover {
  box-shadow: 0 0 12px #000;
  opacity: 1;
  cursor: pointer;
}
.ncf-container .ncf .ncf-title {
  font-weight: 700;
  font-size: 16px;
  text-align: left;
  margin-top: 0;
  margin-bottom: 6px;
  word-wrap: break-word;
}
.ncf-container .ncf .nfc-message {
  margin: 0;
  text-align: left;
  word-wrap: break-word;
}
.ncf-container .success {
  background: #51a351;
  color: #fff;
  padding: 15px 15px 15px 50px;
  background-image: url(data:image/png;base64,);
}
.ncf-container .info {
  background: #2f96b4;
  color: #fff;
  padding: 15px 15px 15px 50px;
  background-image: url(data:image/png;base64,);
}
.ncf-container .warning {
  background: #f87400;
  color: #fff;
  padding: 15px 15px 15px 50px;
  background-image: url(data:image/png;base64,);
}
.ncf-container .error {
  background: #bd362f;
  color: #fff;
  padding: 15px 15px 15px 50px;
  background-image: url(data:image/png;base64,) !important;
}
.ncf-container button {
  position: relative;
  right: -0.3em;
  top: -0.3em;
  float: right;
  font-weight: 700;
  color: #fff;
  text-shadow: 0 1px 0 #fff;
  opacity: 0.8;
  line-height: 1;
  font-size: 16px;
  padding: 0;
  cursor: pointer;
  background: 0 0;
  border: 0;
}
.ncf-container button:hover {
  opacity: 1;
}
